	import slFilter from '@/components/sl-filter/sl-filter.vue';
	import uniRate from "@/components/uni-rate/uni-rate.vue"
    import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default { 
		data() {
			return {
				datalist:[],
				loadingText: '正在加载...',
				hasmore:true,
				keyword:'',
				curpage:1,
				predepoit:'',
				memberpoints:'--', 
				city:'定位中',
				themeColor: '#000000',
				titleColor: '#666666',
				tabIndex: 0,
				tabClasslist: [],
				gc_id:0,
				menuList: [
					{
						'title': '排序',
						'key': 'sort',
						'isSort': true,
						'reflexTitle': true,
						//'defaultSelectedIndex': 0,
						'detailList': [{
								'title': '默认排序',
								'value': '0'
							},
							{
								'title': '按积分奖励',
								'value': 'points'
							},
							{
								'title': '按参与人数',
								'value': 'actmember'
							},
							{
								'title': '按结束时间',
								'value': 'etime'
							}
						]
					}
				],
				bysort:'',
				isable:0
			}				
		},
		components: {slFilter},
        computed: mapState(['hasLogin','userInfo','Location']),  
		onLoad() {
			let _this = this;
			_this.city = _this.Location.city_name;
			console.log('页面加载');
			_this.tabClasslist = [{gc_name: '全部',gc_id: '0'}];
			_this.$Request.post('plat_act/gclist').then(res => {
				if (res.code == 200) {
					console.log(res);
					if(res.datas.gc_list){
						for (let i = 0; i < res.datas.gc_list.length;i++) {
							_this.tabClasslist.push(res.datas.gc_list[i]);
						}
					}
					console.log(_this.tabClasslist);
				}
			});
			_this.loadData();
		},
         onReachBottom() {
             console.log('正在加载中...');
			 this.curpage++;
			 this.loadData();
         },
         onPageScroll(){
             console.log('页面滚动...');
         },
         onPullDownRefresh() {
             console.log('上拉刷新...');
             uni.stopPullDownRefresh();
         },
        methods: {  
			result(val) {
				console.log(val);
				//console.log('filter_result:' + JSON.stringify(val));
				//this.filterResult = JSON.stringify(val, null, 2);
				let _data = JSON.parse(JSON.stringify(val));
				let _keys = Object.keys(_data);
				console.log(_keys);
				if(_keys[0]=='isable'){
					if(_data['isable']!=this.isable){
						this.isable=_data['isable'];
						//this.menuList[1].defaultSelectedIndex=
						this.clearData();
						this.loadData();
					}
				}else if(_keys[0]=='sort'){
					if(_data['sort']!=this.bysort){
						this.bysort=_data['sort'];
						this.clearData();
						this.loadData();
					}
				}
				
				/* for(var key in data){
					console.log(key);
					console.log(data[key]);
				} */
			},
			clearData(){
				this.datalist=[];
				this.loadingText='正在加载...';
				this.hasmore=true;
				this.curpage=1;
			},
            async loadData() {  
				let _this = this;
				console.log('===userInfo==='+this.hasLogin);
				console.log(this.userInfo);
				console.log(this.userInfo.token);
              
					if(!this.hasmore){
						this.loadingText = '暂无更多信息';
						return false;
					}
					let postData={curpage:_this.curpage,cityid:_this.Location.city_id,gc_id:_this.gc_id,keyword:_this.keyword,orderby:_this.bysort};
					_this.$Request.post('plat_act/index',postData).then(res => {
						if (res.code == 200) {
							console.log(res);
							_this.hasmore =res.hasmore;
							console.log(_this.hasmore);
							let _datalist = res.datas.list;
							for(var i=0;i<_datalist.length;i++){
								_this.datalist.push(_datalist[i]);
							}
							if(!res.hasmore){
								_this.loadingText = '暂无更多信息';
								return false;
							}
						}
						else{
							uni.showToast({
								icon: 'none',
								title:res.datas.error,
								duration:2000
							});
							uni.navigateTo({
								url:'login',
							});
						}
					});
				
            },
			submitSearch(){
				this.clearData();
				this.loadData();
			},
			toPage: function(url) {
				if (!this.hasLogin) {
					url="/pages/member/login";
				} 
				uni.navigateTo({
					url:url,
				})
				
			},
			toCity:function(){
				uni.navigateTo({
					url:"/pages/index/city?url=/pages/member/points/pshop"
				})
			},
			tapTab(gcid){
				this.gc_id=gcid;
				this.tabIndex=gcid;
				this.clearData();
				this.loadData();
				
			}

			 
        }  
    }  
